@import-normalize;

body {
  font-family: sans-serif;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

li {
  text-decoration: none;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  /* height: auto; */
}

th {
  padding: 0;
}

table {
  border-collapse: collapse;
}
